@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.9/css/weather-icons.min.css);
@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}
html {
    background-image: url(/Weather-App/static/media/bg-image.10dc1c7f.jpg);
    background-size: cover;
    background-position: fixed;
    background-repeat: no-repeat;
}
.tagline h1 {
    text-align: center;
    padding: 15px;
    color: #e6e6ea;
}
header {
    display: flex;
    justify-content: space-around;
    width: 80%;
    margin: 2rem auto;
}
.input-fields {
    width: 26%;
    border: 0px;
    border-bottom: 1.5px solid black;
    background-color: transparent;
    color: #fff;
    font-size: 22px;
    padding: 3px 15px;
    outline: none;
}
.weather-btn {
    outline: none;
    font-size: 20px;
    padding: 7px;
    border-radius: 5px;
    border: 0px;
    background-color: #007bff;
    color: #fff;
    box-shadow: 0px 0px 5px #000000be;
}
.weather-btn:hover {
    background-color: #0563c9;
    cursor: pointer;
}
input:focus {
    border-color: white;
}
.clear-btn {
    font-size: 20px;
    padding: 7px;
    border-radius: 5px;
    color: #fff;
    background-color: #dc3545;
    border: 1px solid #dc3545;
    box-shadow: 0px 0px 5px #000000be;
}
.clear-btn:hover {
    cursor: pointer;
    background-color: #e92e40;
}
::-webkit-input-placeholder {
    color: #bdc5eb;
}
:-ms-input-placeholder {
    color: #bdc5eb;
}
::-ms-input-placeholder {
    color: #bdc5eb;
}
::placeholder {
    color: #bdc5eb;
}
.location {
    padding: 10px;
    font-size: 40px;
    text-transform: capitalize;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #d4f8e8;
}
.weather-icon {
    font-size: 80px;
    color: greenyellow;
}
.weather-details {
    margin: 10vh auto;
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    height: auto;
    background-color: #0000005d;
    color: #d4f8e8;
    border-radius: 40px;
    box-shadow: 0px 0px 30px #000000cc;
}
.weather-icon {
    font-size: 80px;
    color: greenyellow;
    padding: 10px;
}

.temp {
    display: flex;
    justify-content: space-around;
    width: 20vw;
    font-size: 40px;
    padding: 5px;
}
.wind-speed-humidity {
    width: 85%;
    display: flex;
    justify-content: space-around;
    font-size: 25px;
}
.wind-speed {
    display: flex;
    width: 35%;
    justify-content: space-between;
    align-items: center;
}
.wind-speed-icon {
    font-size: 50px;
}
.humidity {
    font-size: 30px;
    display: flex;
    width: 26%;
    justify-content: space-around;
    align-items: center;
}
.weather-description {
    font-size: 30px;
    text-transform: capitalize;
    padding: 20px;
}
footer {
    color: white;
    margin-top: 11%;
    font-size: 1.1em;
    padding: 10px 2vw;
}
footer i {
    color: #ff42e6;
}
@media screen and (max-width: 890px) {
    .weather-details {
        justify-content: space-between;
    }
    .input-fields {
        border-bottom: 1.5px solid black;
        font-size: 20px;
        width: 28%;
        justify-content: space-evenly;
    }
    .weather-details {
        width: 80%;
    }
    .weather-btn {
        font-size: 18px;
    }
    .clear-btn {
        font-size: 18px;
    }
    .temp {
        justify-content: space-between;
        width: 30vw;
        font-size: 37px;
    }
    .wind-speed-humidity {
        width: 80%;
        justify-content: space-around;
        font-size: 25px;
    }
    .wind-speed {
        display: flex;
        width: 40%;
        justify-content: space-between;
    }
    .wind-speed-icon {
        font-size: 45px;
    }
    .humidity {
        font-size: 30px;
        display: flex;
        width: 26%;
        justify-content: space-around;
        align-items: center;
    }
    .weather-description {
        font-size: 28px;
        padding: 18px;
    }
}
@media screen and (max-width: 720px) {
    .header {
        justify-content: space-between;
    }
    .input-fields {
        font-size: 1em;
        width: 26%;
        flex-shrink: 0.8;
        font-size: 18px;
    }
    .weather-btn {
        font-size: 15px;
        flex-shrink: 2;
    }
    .clear-btn {
        font-size: 15px;
        flex-shrink: 2;
    }
    .weather-details {
        width: 80%;
    }
    .temp {
        justify-content: space-around;
        width: 45vw;
        font-size: 38px;
    }
    .wind-speed-humidity {
        width: 80%;
        justify-content: space-around;
        font-size: 25px;
    }
    .wind-speed {
        justify-content: space-between;
        width: 45%;
    }
    .wind-speed-icon {
        font-size: 45px;
    }
    .humidity {
        font-size: 26px;
        width: 26%;
        justify-content: space-between;
    }
    .weather-description {
        font-size: 28px;
        padding: 20px;
    }
}
@media screen and (max-width: 550px) {
    header {
        width: 95%;
        flex-direction: column;
        align-items: center;
    }
    .input-fields {
        font-size: 1.4em;
        width: 90%;
        flex-shrink: 0.8;
        padding: 10px;
        margin: 10px;
    }
    .weather-btn {
        width: 80%;
        margin: 30px;
        font-size: 20px;
        padding: 10px;
    }
    .clear-btn {
        margin: 10px;
        width: 80%;
        font-size: 20px;
        padding: 10px;
    }
    .weather-details {
        width: 95%;
    }
    .temp {
        justify-content: space-between;
        width: 45vw;
        font-size: 38px;
    }
    .wind-speed-humidity {
        width: 90%;
        justify-content: space-between;
        font-size: 25px;
    }
    .wind-speed {
        justify-content: space-between;
    }
    .wind-speed-icon {
        font-size: 40px;
    }
    .humidity {
        font-size: 24px;
        width: 25%;
        justify-content: space-between;
    }
    .weather-description {
        font-size: 25px;
        padding: 18px;
    }
}

